import React from "react";
import {
  VerticalTimeline,
  VerticalTimelineElement,
} from "react-vertical-timeline-component";
import "react-vertical-timeline-component/style.min.css";
import { MdSchool } from "react-icons/md";
import { FaUniversity } from "react-icons/fa";
import { LuSchool } from "react-icons/lu";
// import { BiSolidInstitution } from "react-icons/bi";
import { FaComputer } from "react-icons/fa6";

import "./Education.css";

const Education = () => {
  return (
    <div className="education" id="education">
      <h2>Education Details</h2>
      <hr />
      <VerticalTimeline lineColor="#138781">
        <VerticalTimelineElement
          className="vertical-timeline-element--work trigger dark-vertical-timeline-element"
          contentStyle={{ background: "white", color: "black" }}
          contentArrowStyle={{ borderRight: "7px solid  #138781" }}
          date="2022 - 2023"
          iconStyle={{ background: "#e17228", color: "white" }}
          icon={<FaComputer />}
        >
          <div className="time-line-content">
            <h3 className="vertical-timeline-element-title">
              Fullstack Web Development
            </h3>
            <h4 className="vertical-timeline-element-subtitle">
              CCBP, Nxtwave
            </h4>
            <p>
              I successfully completed an intensive full-stack development
              course at CCBP NXTWAVE between 2022 and 2023, equipping me with a
              comprehensive skill set in both front-end and back-end
              technologies.
            </p>
          </div>
        </VerticalTimelineElement>

        <VerticalTimelineElement
          className="vertical-timeline-element--work trigger"
          contentStyle={{ background: "white", color: "black" }}
          contentArrowStyle={{ borderRight: "7px solid  #138781" }}
          date="2018 - 2021"
          iconStyle={{ background: "#e17228", color: "white" }}
          icon={<FaUniversity />}
        >
          <div className="time-line-content">
            <h3 className="vertical-timeline-element-title">
              BSc Computers Science
            </h3>
            <h4 className="vertical-timeline-element-subtitle">
              Kalinga University, Raipur
            </h4>
            <p>
              I earned my Bachelor of Science degree in Computer Science from
              Kalinga University between 2018 and 2021, where I delved into a
              diverse range of topics, including algorithms, data structures,
              programming languages, and software development methodologies.
            </p>
          </div>
        </VerticalTimelineElement>

        <VerticalTimelineElement
          className="vertical-timeline-element--work trigger"
          contentStyle={{ background: "white", color: "black" }}
          contentArrowStyle={{ borderRight: "7px solid  #138781" }}
          date="2019 - 2021"
          iconStyle={{ background: "#e17228", color: "white" }}
          icon={<MdSchool />}
        >
          <div className="time-line-content">
            <h3 className="vertical-timeline-element-title">
              Mechanical Enginnering
            </h3>
            <h4 className="vertical-timeline-element-subtitle">
              Sri Chaithanya Engg College, VSKP
            </h4>
            <p>
              I successfully completed my Diploma in Mechanical Engineering at
              Sri Chaithanya Engineering College, Visakhapatnam, between 2014
              and 2018, where I gained a comprehensive understanding of
              mechanical principles and engineering practices.
            </p>
          </div>
        </VerticalTimelineElement>

        <VerticalTimelineElement
          className="vertical-timeline-element--work trigger"
          contentStyle={{ background: "white", color: "black" }}
          contentArrowStyle={{ borderRight: "7px solid  #138781" }}
          date="2014"
          iconStyle={{ background: "#e17228", color: "white" }}
          icon={<LuSchool />}
        >
          <div className="time-line-content">
            <h3 className="vertical-timeline-element-title">
              Secondary Education
            </h3>
            <h4 className="vertical-timeline-element-subtitle">
              ZPHS MAIN high school, NRPM
            </h4>
            <p>
              I completed my secondary education at ZPHS Main High School in
              2014, where I developed a strong foundation in academic excellence
              and personal growth. I am grateful for the opportunities and
              experiences I gained during my time at ZPHS Main High School, as
              they have shaped me into the dedicated and motivated individual I
              am today.
            </p>
          </div>
        </VerticalTimelineElement>
      </VerticalTimeline>
    </div>
  );
};

export default Education;
