import React from "react";
import "./About.css";
import Jump from "react-reveal/Jump";
import aboutImg from "../../images/aboutMe.jpg";

const About = () => {
  return (
    <>
      <Jump>
        <div className="container about-container" id="about">
          <div className="row about-img-content">
            <div className="col-md-6  about-img">
              <img className="about-img" src={aboutImg} alt="AboutImg" />
            </div>
            <div className="col-md-6 about-content">
              <h1>About Me</h1>
              <p>
                As a recent graduate of the Full Stack Development course at
                CCBP NXTWAVE Institution, I have acquired a comprehensive skill
                set in web development. Proficient in HTML, CSS, Python,
                JavaScript, React.js, Node.js, MongoDB, and SQLite, I am
                equipped to tackle diverse challenges in the digital landscape.
                My journey through this intensive program has not only honed my
                technical abilities but also instilled in me a passion for
                creating dynamic and user-centric web applications. With a
                strong foundation in both front-end and back-end technologies, I
                am excited to leverage my expertise to build innovative
                solutions and contribute to meaningful projects. My commitment
                to continuous learning and problem-solving drives me to stay
                abreast of emerging trends and best practices in the
                ever-evolving field of web development.
              </p>
            </div>
          </div>
        </div>
      </Jump>
    </>
  );
};

export default About;
