import React from "react";
import "./Techstack.css";
import { TechstackList } from "../../utils/TechstackList";
import Fade from "react-reveal/Fade";

const Techstack = () => {
  return (
    <>
      <div className="container tech-stack" id="techStack">
        <h2>Technoligies Stack</h2>
        <hr />
        <p>👉 Below are the technologies I use to build web applications:</p>
        <div className="row">
          {TechstackList.map((tech) => {
            return (
              <Fade left>
                <div className="col-md-3" key={tech._id}>
                  <div className="card mb-3">
                    <div className="card-content">
                      <div className="card-body">
                        <div className="media d-flex justify-content-center">
                          <tech.icon className="tech-icon mt-1 me-2" />
                          <div>{tech.name}</div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </Fade>
            );
          })}
        </div>
      </div>
    </>
  );
};

export default Techstack;
