import React from "react";
import Typewriter from "typewriter-effect";
import "./Home.css";
import Resume from "../../assets/docs/hanumanthResume2024.pdf";
import Fade from "react-reveal/Fade";

const Home = () => {
  return (
    <>
      <div className="container-fluid home-container" id="home">
        <div className="container home-content">
          <Fade right>
            <h2>Hii👋 I'm a</h2>
            <h1>
              <Typewriter
                options={{
                  strings: ["Hanumanthu Pyla", "Mernstack Developer!"],
                  autoStart: true,
                  loop: true,
                }}
              />
            </h1>
          </Fade>
          <Fade bottom>
            <div className="home-buttons">
              <a
                className="btn btn-hire"
                href="https://api.whatsapp.com/send?phone=7396474611"
                rel="noreferrer"
                target="_blank"
              >
                Hire Me
              </a>
              <a
                className="btn btn-cv"
                href={Resume}
                title="download"
                download="resume.pdf"
              >
                Resume
              </a>
            </div>
          </Fade>
        </div>
      </div>
    </>
  );
};

export default Home;
